// type is 'success' or 'error
export const showAlert = (type, msg) => {
  pxmu.toast({
    msg,
    status: type,
    time: 2500,
    bg: type === 'success' ? 'rgba(1, 203, 92)' : 'rgba(239, 0, 27)',
    color: '#fff',
    location: 'top',
    animation: 'slidedown',
  });
};

export const isLoading = (msg, flag) => {
  // let bg =
  //   msg === '!!!' ? 'rbga(1,200,91,0.65)' : 'rgba(0, 0, 0, 0.65)';

  pxmu.loading({
    msg, //loading 
    time: 2500, //
    // 1, 200, 91
    bg: 'rbga(1,200,91)', //
    color: '#fff', //
    animation: 'fade', // 
    close: false, //  false pxmu.closeload();
    inscroll: false, // 
    inscrollbg: 'rgba(0, 0, 0, 0.45)', // 
  });

  if (flag !== 1) pxmu.closeload(100); //100 0
};
